/* ==========================================================================
   Elated Membership Plugin Style - begin
   ========================================================================== */
.eltd-membership-response-holder .eltd-membership-response {
    text-align: center;
    padding: 20px;
    color: #fff;
    font-size: 16px;
}

.eltd-membership-response-holder .eltd-membership-response.eltd-membership-message-succes {
    background-color: #ff4d30;
    padding: 10px;
}

.eltd-membership-response-holder .eltd-membership-response.eltd-membership-message-succes .eltd-membership-response-message p{
    color: #fff;
}

.eltd-membership-response-holder .eltd-membership-response.eltd-membership-message-error {
    background-color: #ff4d30;
}
.eltd-membership-response-holder .eltd-membership-response.eltd-membership-message-error .eltd-membership-response-message p{
    color: #fff;
}

/* Login/Register widget - start
   ========================================================================== */
.eltd-login-register-widget {
    display: inline-block;
    position: relative;
    vertical-align: middle;
    height: 100%;
}

.eltd-top-bar-widget-area .eltd-login-register-widget {
    float: left;
    padding-right: 9px;
}

.eltd-login-register-widget .eltd-logged-in-user {
    height: 100%;
    vertical-align: middle;
    display: inline-block;
}

.eltd-login-register-widget .eltd-logged-in-user > span {
    display: inline-block;
    line-height: 34px;
    font-size: 12px;
    color: #fff;
    text-transform: uppercase;
    border-radius: 20px;
    padding: 1px 5px 0 12px;
    cursor: pointer;
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -ms-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.eltd-login-register-widget .eltd-logged-in-user > span:hover {
    color: #fff;
}

.eltd-login-register-widget .eltd-logged-in-user > span img {
    width: 24px;
    height: 24px;
    float: left;
    margin-top: 5px;
    margin-right: 10px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    display: inline-block;
    vertical-align: middle;
}

.eltd-login-register-widget:hover .eltd-login-dropdown {
    visibility: visible;
    opacity: 1;
}

.eltd-login-register-widget .eltd-login-dropdown {
    opacity: 0;
    list-style-type: none;
    position: absolute;
    background-color: #242625;
    width: 100%;
    bottom: 0;
    margin: 0;
    right: 0;
    text-align: left;
    -webkit-transform: translateY(100%);
    -moz-transform: translateY(100%);
    -ms-transform: translateY(100%);
    -o-transform: translateY(100%);
    transform: translateY(100%);
    padding: 10px 21px;
    min-width: 120px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    visibility: hidden;
    -webkit-transition: opacity 0.3s ease;
    -moz-transition: opacity 0.3s ease;
    -ms-transition: opacity 0.3s ease;
    -o-transition: opacity 0.3s ease;
    transition: opacity 0.3s ease;
}

.eltd-login-register-widget .eltd-login-dropdown li {
    padding: 4px 0;
}

.eltd-login-register-widget .eltd-login-dropdown li:hover a {
    color: #fff;
}

.eltd-login-register-widget .eltd-login-dropdown li a {
    color: #bbb;
    font-size: 13px;
}

.eltd-login-register-holder {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.85);
    z-index: 1002;
    display: none;
}

.eltd-login-register-holder .eltd-login-register-content {
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    transform: translateY(-50%);
    border: none;
    margin-top: 0;
}

.eltd-login-register-content {
    width: 330px;
    position: relative;
    margin: 0 auto;
    background-color: #fff;
}

.eltd-login-register-content ul {
    list-style: none;
    margin: 10px 0;
    padding: 0;
    text-align: center;
}

.eltd-login-register-content ul li {
    display: inline-block;
    padding: 0 10px;
}

.eltd-login-register-content .eltd-social-login-holder-inner,
.eltd-login-register-content .eltd-social-register-holder-inner,
.eltd-login-register-content .eltd-register-notice {
    padding: 40px 28px;
}

.eltd-login-register-content .eltd-reset-pass-content-inner {
    padding: 20px;
}

.eltd-login-register-content .eltd-login-form-social-login {
    background-color: #f9f9f9;
    border-top: 1px solid #ebebeb;
    padding: 23px 0 34px;
    margin: 12px 0 0;
    text-align: center;
}

.eltd-login-register-content .eltd-login-form-social-login .eltd-login-social-title {
    margin-bottom: 15px;
}

.eltd-login-register-content .eltd-login-form-social-login form {
    display: inline-block;
    margin: 0 5px;
}

.eltd-login-register-content .eltd-login-form-social-login .eltd-google-login .eltd-btn-text,
.eltd-login-register-content .eltd-login-form-social-login .eltd-facebook-login .eltd-btn-text {
    margin: 0;
}

.eltd-login-register-content .eltd-login-form-social-login .eltd-facebook-login,
.eltd-login-register-content .eltd-login-form-social-login .eltd-google-login {
    padding: 0;
}

.eltd-login-register-content .eltd-login-form-social-login .eltd-facebook-login .eltd-btn-text,
.eltd-login-register-content .eltd-login-form-social-login .eltd-google-login .eltd-btn-text {
    line-height: 30px;
    padding: 0 12px 0 10px;
}

.eltd-login-register-content .eltd-login-form-social-login .eltd-facebook-login .eltd-btn-icon-holder,
.eltd-login-register-content .eltd-login-form-social-login .eltd-google-login .eltd-btn-icon-holder {
    float: left;
    font-size: 13px;
    padding: 6px 10px;
    border-right: 1px solid rgba(255, 255, 255, 0.2);
}

.eltd-login-register-content .eltd-login-form-social-login .eltd-facebook-login .eltd-btn-icon-holder .eltd-btn-icon-elem,
.eltd-login-register-content .eltd-login-form-social-login .eltd-google-login .eltd-btn-icon-holder .eltd-btn-icon-elem {
    vertical-align: baseline;
}

/* Login/Register widget - end
   ========================================================================== */
/* Dashboard page - start
   ========================================================================== */


.eltd-membership-dashboard-nav-holder {
    position: relative;
    display: block;
    vertical-align: middle;
    width: 100%;
}

.eltd-membership-dashboard-content-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: 100%;
    margin: 0;
}

.eltd-membership-dashboard-content-holder .eltd-membership-dashboard-page-content p {
    font-size: 15px;
    padding: 0;
}

.eltd-membership-dashboard-content-holder .eltd-membership-dashboard-page-content p span {
    font-weight: 400;
    font-size: 18px;
    display: block;
    color: #353535;
    margin-bottom: 7px;
}

.eltd-membership-dashboard-nav {
    display: block;
    position: relative;
    list-style: none outside none;
    margin: 0;
    overflow: hidden;
    vertical-align: middle;
}

.eltd-membership-dashboard-nav li {
    display: inline-block;
    vertical-align: middle;
}

.eltd-membership-dashboard-nav li a {
    position: relative;
    display: block;
    text-align: center;
    margin: 0;
    box-sizing: border-box;
}


.eltd-membership-dashboard-page {
    display: table;
    width: 100%;
    margin-bottom: 80px;
}

/* Dashboard page - end
